const initDropdown = () => {
  const guaranteeTypesBtn = document.querySelectorAll('.js-guarantee-types-btn');
  const guaranteeTypesContent = document.querySelector('.js-guarantee-types-content');

  if (guaranteeTypesBtn.length) {

    guaranteeTypesBtn.forEach((btn) => {

      btn.addEventListener('click', function () {
        guaranteeTypesContent.classList.toggle('visually-hidden');
      });

    });

  }

  const cards = document.querySelectorAll('.js-card');

  if (cards.length) {

    cards.forEach((card) => {
      const btn = card.querySelector('.js-btn-dropdown');
      const elements = card.querySelectorAll('.js-change-content');

      btn.addEventListener('click', function () {

        elements.forEach((el) => {
          let className = el.dataset.dropdown;
          el.classList.toggle(className);
        });

      });
    });

  }

};

export {initDropdown};
