const initSlider = () => {
  const sliderContainer = document.querySelector('.swiper-container');
  const sliderPagination = document.querySelector('.swiper-pagination');
  const sliderBtnNext = document.querySelector('.swiper-button-next');
  const sliderBtnPrev = document.querySelector('.swiper-button-prev');

  const initSwiper = () => {
    // eslint-disable-next-line no-unused-vars
    const swiperReviews = new Swiper('.swiper-container', {

      loop: false,
      slidesPerView: 1,

      pagination: {
        el: '.swiper-pagination',
        clickable: true,
      },

      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
      },
    });
  };

  if (sliderContainer && sliderPagination && sliderBtnNext && sliderBtnPrev) {
    initSwiper();
  }
};

export {initSlider};

